import React from "react"
import { Form, Input, Button, Select, InputNumber } from "antd"
import { MinusCircleOutlined, PlusOutlined } from "@ant-design/icons"

const CreateForm = ({ onFinish }) => {
  const [form] = Form.useForm()

  return (
    <div
      style={{
        display: "flex",
        padding: "20px",
        textAlign: "center",
        flexDirection: "column",
        height: "100%",
        minHeight: "100vh",
        background: "#f7f7f7",
      }}
    >
      <Form
        form={form}
        labelCol={{ span: 10 }}
        initialValues={{ games: [{ type: "redirect" }] }}
        onFinish={onFinish}
        style={{ maxWidth: 500, margin: "auto", width: "100%" }}
      >
        <Form.List name="games">
          {(fields, { add, remove }) => (
            <>
              {fields.map((field, index) => {
                return (
                  <div
                    style={{ padding: 20, background: "#fefefe", marginBottom: 20, border: "1px solid #f0f0f0", width: "100%" }}
                    key={index}
                  >
                    <Form.Item {...field} required={false} name={[field.name, "type"]}>
                      <Select style={{ width: "100%" }} disabled={index === fields.length - 1}>
                        <Select.Option value="question">Question</Select.Option>
                        <Select.Option value="simonSays">Simon Says</Select.Option>
                        <Select.Option value="buttonClicks">Button Clicks</Select.Option>
                        <Select.Option value="redirect">Redirect</Select.Option>
                      </Select>
                    </Form.Item>
                    <Form.Item noStyle shouldUpdate={true}>
                      {({ getFieldValue }) => {
                        return getFieldValue(["games", field.name, "type"]) === "question" ? (
                          <>
                            <Form.Item name={[field.name, "question"]}>
                              <Input placeholder="Your question here" />
                            </Form.Item>
                            <Form.Item name={[field.name, "hint"]}>
                              <Input placeholder="Your hits here" />
                            </Form.Item>
                            <Form.Item name={[field.name, "answers"]}>
                              <Input placeholder="Comma separated answers" />
                            </Form.Item>
                          </>
                        ) : null
                      }}
                    </Form.Item>
                    <Form.Item noStyle shouldUpdate={true}>
                      {({ getFieldValue }) => {
                        return getFieldValue(["games", field.name, "type"]) === "simonSays" ? (
                          <>
                            <Form.Item label={"Rounds to win"} name={[field.name, "rounds"]}>
                              <InputNumber min={2} max={100} placeholder="6" />
                            </Form.Item>
                            <Form.Item label={"Options to play with"} name={[field.name, "options"]}>
                              <InputNumber min={1} max={6} placeholder="4" />
                            </Form.Item>
                          </>
                        ) : null
                      }}
                    </Form.Item>
                    <Form.Item noStyle shouldUpdate={true}>
                      {({ getFieldValue }) => {
                        return getFieldValue(["games", field.name, "type"]) === "buttonClicks" ? (
                          <Form.Item label={"Rounds to win"} name={[field.name, "clicks"]}>
                            <InputNumber min={2} max={10000} placeholder="20" />
                          </Form.Item>
                        ) : null
                      }}
                    </Form.Item>
                    <Form.Item noStyle shouldUpdate={true}>
                      {({ getFieldValue }) => {
                        return getFieldValue(["games", field.name, "type"]) === "redirect" ? (
                          <>
                            <Form.Item name={[field.name, "url"]}>
                              <Input type="url" placeholder="Your URL here" />
                            </Form.Item>
                          </>
                        ) : null
                      }}
                    </Form.Item>
                    {index < fields.length - 1 && (
                      <MinusCircleOutlined
                        style={{
                          margin: "8px",
                          cursor: "pointer",
                          fontSize: "24px",
                          color: "#999",
                          transition: "all 0.3s",
                        }}
                        onClick={() => remove(field.name)}
                      />
                    )}
                  </div>
                )
              })}
              <Form.Item>
                <Button type="dashed" onClick={() => add({}, fields.length - 1)} icon={<PlusOutlined />}>
                  Add Game
                </Button>
              </Form.Item>
            </>
          )}
        </Form.List>
        <Form.Item>
          <Button type="primary" htmlType="submit">
            Create &amp; Copy
          </Button>
        </Form.Item>
      </Form>
    </div>
  )
}

export default CreateForm
