import React from "react"
import { encodeObj, copyToClipboard } from "../../utils"
import { message } from "antd"
import CreateForm from "./CreateForm"

const Home = () => (
  <CreateForm
    onFinish={(values) => {
      const newUrl = window.location + encodeObj(values.games)
      copyToClipboard(newUrl)
      message.success("URL Copied")
    }}
  />
)

export default Home
